import { Card, CardContent } from "@/components/ui/card";
import { Radar } from "@/components/ui/radar";

export default function Page() {
    return (
        <div className="p-6 space-y-6 bg-black text-white min-h-screen">
            <h1 className="text-4xl font-bold text-center">Strength Progress</h1>
            
            <div className="flex flex-col items-center">
                <h2 className="text-lg font-medium">Progress Chart:</h2>
                <Radar
                    data={{
                        labels: ["Shoulders", "Chest", "Quads", "Hamstrings", "Lats", "Arms"],
                        datasets: [{
                            label: "Strength",
                            data: [80, 70, 85, 75, 90, 80],
                        }],
                    }}
                />
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <Card>
                    <CardContent className="p-4">
                        <h3 className="text-lg font-medium">Barbell Rows</h3>
                        <p className="text-2xl font-bold">120kg</p>
                        <p className="text-green-500">1Y +3.1%</p>
                    </CardContent>
                </Card>
                <Card>
                    <CardContent className="p-4">
                        <h3 className="text-lg font-medium">Incline DB Press</h3>
                        <p className="text-2xl font-bold">50kg</p>
                        <p className="text-green-500">1Y +1.5%</p>
                    </CardContent>
                </Card>
                <Card>
                    <CardContent className="p-4">
                        <h3 className="text-lg font-medium">Bulgarian Split Squad</h3>
                        <p className="text-2xl font-bold">180kg</p>
                        <p className="text-green-500">1Y +6.3%</p>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}